export function calculatePositionChange(distance, rotation, positionX, positionY) {
  console.log(`${distance}`);
  const rotationRadians = rotation === 0 ? 0 : Math.PI * rotation / 180;
  const distanceY = distance * Math.cos(rotationRadians) * -1;
  const distanceX = distance * Math.sin(rotationRadians);
  return {
    positionX: positionX + distanceX,
    positionY: positionY + distanceY
  }
}

export function calculateShipPosition(ship, speed) {
  // dy = d * cos(rotation)
  // dx = d * sin(rotation)
  // could the compute cos and sin of angles up front in a map:
  // rotation -> {cos(rotation), sin(rotation)}
  const {positionX, positionY} = calculatePositionChange(
    speed,
    ship.get('rotation'),
    ship.get('positionX'),
    ship.get('positionY'));
  let updatedState = ship.set('positionX', positionX);
  updatedState = updatedState.set('positionY', positionY);
  return updatedState;
}


