import React, {PureComponent} from 'react';

import Sprite from './sprite';

class Bullet extends PureComponent {

  render() {
    const {data} = this.props;
    const height = data.get('height');
    const width = data.get('width');
    const src = `bullet_${height}x${width}.png`;
    const rotation = data.get('rotation');
    const positionX = data.get('positionX');
    const positionY = data.get('positionY');
    return (
      <div className="outline"
           style={{
             position: 'fixed',
             transform: `rotate(${rotation}deg)`,
             height: height,
             width: width,
             top: positionY,
             left: positionX
           }}>
        <Sprite
          src={src}
          height={height}
          width={width}
          top={positionY}
          left={positionX}
        />
      </div>
    );
  }
}

export default Bullet;


