import {Map, List} from 'immutable';

import doCirclesOverlap from '../api/doCirclesOverlap';

// TODO: this is not accurate, need to figure out a better way of calculating this stuff
// http://stackoverflow.com/questions/12066870/how-to-check-if-an-element-is-overlapping-other-elements
function findOverlapAsRectangles(obj1, obj2) {
  const obj1Height = obj1.get('height');
  const obj1Width = obj1.get('width');
  const obj1X = obj1.get('positionX');
  const obj1Y = obj1.get('positionY');
  const obj2Height = obj2.get('height');
  const obj2Width = obj2.get('width');
  const obj2X = obj2.get('positionX');
  const obj2Y = obj2.get('positionY');
  const obj1Left = obj1X;
  const obj1Right = obj1X + obj1Width;
  const obj2Left = obj2X;
  const obj2Right = obj2X + obj2Width;
  const obj1Top = obj1Y;
  const obj1Bottom = obj1Y + obj1Height;
  const obj2Top = obj2Y;
  const obj2Bottom = obj2Y + obj2Height;

  const doesNotOverlap = (
  obj2Left > obj1Right
  || obj2Top > obj1Bottom
  || obj2Right < obj1Left
  || obj2Bottom < obj1Top);

  !doesNotOverlap && console.log(`collision found, at least one is true: 
    ${obj2Left} <= ${obj1Right},
    ${obj2Top} <= ${obj1Bottom},
    ${obj2Right} >= ${obj1Left},
    ${obj2Bottom} >= ${obj1Top}`);
  return !doesNotOverlap;
}

function findOverlap(obj1, obj2) {
  const obj1Height = obj1.get('height');
  const obj1Width = obj1.get('width');
  const obj1X = obj1.get('positionX');
  const obj1Y = obj1.get('positionY');
  const obj2Height = obj2.get('height');
  const obj2Width = obj2.get('width');
  const obj2X = obj2.get('positionX');
  const obj2Y = obj2.get('positionY');
  // use overlapping circles to determine if there's a collision
  // create circles for each obj
  const obj1Radius = obj1Width >= obj1Height ? obj1Width / 2 : obj1Height / 2;
  const circle1 = {
    center: {top: obj1Y + obj1Radius, left: obj1X + obj1Radius},
    radius: obj1Radius
  };
  const obj2Radius = obj2Width >= obj2Height ? obj2Width / 2 : obj2Height / 2;
  const circle2 = {
    center: {top: obj2Y + obj2Radius, left: obj2X + obj2Radius},
    radius: obj2Radius
  };
  // find if the circles overlap
  // TODO: find direction/side that the collision happened?
  return doCirclesOverlap(circle1, circle2);
}

export function collisionDetector(state) {
  const projectiles = state.get('projectiles');
  const ships = state.get('ships');
  const updatedStateWithCollisions = projectiles.reduce((updatedState, projectile, projectileIndex) => {
    const existingCollisionsDetected = updatedState.get('collisions');
    const collisionsFound = ships.map((ship, shipIndex) => {
      const overlapDetected = findOverlap(projectile, ship);
      if (overlapDetected) {
        console.log(`collision detected`);
        // console.log(`collision detected: projectile: ${projectile}, ship: ${ship}`);
        // return a collision record to be resolved
        return new Map({
          projectile, projectileIndex, ship, shipIndex
        });
      }
    });
    return updatedState.set('collisions', collisionsFound.concat(existingCollisionsDetected));
  }, state);
  return updatedStateWithCollisions;
}


