import {Map, List} from 'immutable';

const initialState = new Map({
  ship: new Map({
    height: 40,
    width: 40,
    rotation: 135,
    positionX: 0,
    positionY: 100
  }),
  ships: new List([new Map({
    height: 40,
    width: 40,
    rotation: 90,
    positionX: 200,
    positionY: 500
  }), new Map({
    height: 40,
    width: 40,
    rotation: 0,
    positionX: 600,
    positionY: 200
  }), new Map({
    height: 40,
    width: 40,
    rotation: 135,
    positionX: 300,
    positionY: 400
  })]),
  keysDown: new Map(),
  projectiles: new List(),
  collisions: new List(),
  planets: new List([new Map({
    height: 120,
    width: 120,
    positionX: 400,
    positionY: 400,
    type: 'blue',
    name: 'Scorpion',
    id: 1
  })]),
  messages: new Map({
    count: 0,
    list: new List([new Map({
      text: 'Welcome to the game',
      id: 0,
      ticks: 50
    })])
  })
});

export default initialState;


