import {Map, List} from 'immutable';

import {calculateShipPosition} from './movement';

export const TURN_SHIP = 'TURN_SHIP';
export const MOVE_SHIP_BW = 'MOVE_SHIP_BW';
export const MOVE_SHIP_FW = 'MOVE_SHIP_FW';
export const FIRE_WEAPON = 'FIRE_WEAPON';

function buildKeysDownActions(keysDown) {
  const defaultAmount = 7;
  const activeActions = [];
  if (keysDown.has('w')) {
    activeActions.push({
      type: MOVE_SHIP_FW,
      speed: 7
    });
  }
  if (keysDown.has('s')) {
    activeActions.push({
      type: MOVE_SHIP_BW,
      speed: -4
    });
  }
  if (keysDown.has('a')) {
    activeActions.push({
      type: TURN_SHIP,
      amount: -defaultAmount
    });
  }
  if (keysDown.has('d')) {
    activeActions.push({
      type: TURN_SHIP,
      amount: defaultAmount
    });
  }
  return new List(activeActions);
}

export function onTickShip(state) {
  const keysDown = state.get('keysDown');
  const activeKeysDown = keysDown.filter((value, key) => value);

  // update ship state
  const activeActions = buildKeysDownActions(activeKeysDown);
  return activeActions.reduce((updatedState, action) => {
    switch (action.type) {
      case TURN_SHIP:
        return updatedState.setIn(['ship', 'rotation'],
          updatedState.getIn(['ship', 'rotation']) + action.amount);
      case MOVE_SHIP_FW:
      case MOVE_SHIP_BW:
        return updatedState.set('ship',
          calculateShipPosition(updatedState.get('ship'), action.speed));
    }
  }, state);
}


