import {Map, List} from 'immutable';

import {calculatePositionChange} from './movement';

function calculateNewPosition(state, speed) {
  // dy = d * cos(rotation)
  // dx = d * sin(rotation)
  // could the compute cos and sin of angles up front in a map:
  // rotation -> {cos(rotation), sin(rotation)}
  const {positionX, positionY} = calculatePositionChange(
    speed,
    state.get('rotation'),
    state.get('positionX'),
    state.get('positionY'));
  let updatedState = state.set('positionX', positionX);
  updatedState = updatedState.set('positionY', positionY);
  return updatedState;
}

export function onTickShips(state) {
  return state;
}


