import {Map, List} from 'immutable';

export function fireWeapon(state) {
  const ship = state.get('ship');
  const rotation = ship.get('rotation'); // comes out same rotation as ship
  const positionX = ship.get('positionX');
  const positionY = ship.get('positionY');

  const newProjectileProps = new Map({
    rotation, positionX, positionY,
    height: 40,
    width: 40,
    speed: 15
  });

  const currentProjectiles = state.get('projectiles');
  const updatedProjectiles = currentProjectiles.push(newProjectileProps);
  return state.set('projectiles', updatedProjectiles);
}


