import {Map, Set} from 'immutable';

export const ADD = 'ADD';
export const ADD_SHIP = 'ADD_SHIP';
export const UNHANDLED = 'UNHANDLED';
export const KEY_DOWN = 'KEY_DOWN';
export const MOVE_KEY_DOWN = 'MOVE_KEY_DOWN';
export const KEY_UP = 'KEY_UP';
export const MOVE_KEY_UP = 'MOVE_KEY_UP';
export const TICK = 'TICK';
export const ATTEMPT_TO_LAND = 'ATTEMPT_TO_LAND';

export function add(num) {
  return {type: ADD, num}
}

export function attemptToLandOnPlanet(ship) {
  return {
    type: ATTEMPT_TO_LAND,
    ship: ship
  }
}

export function addShip() {
  const rotation = getRandomArbitrary(0, 360);
  const x = getRandomArbitrary(10, 800);
  const y = getRandomArbitrary(100, 800);
  return {
    type: ADD_SHIP,
    ship: new Map({
      height: 40,
      width: 40,
      rotation: rotation,
      positionX: x,
      positionY: y
    })
  }
}

function getRandomArbitrary(min, max) {
  return Math.random() * (max - min) + min;
}

const MOVEMENT_KEYS = new Set(['w', 'a', 's', 'd']);

export function handleInputKeyDown(keyDown) {
  let actionType;
  if (MOVEMENT_KEYS.has(keyDown)) {
    actionType = MOVE_KEY_DOWN
  } else {
    actionType = KEY_DOWN
  }
  return {
    type: actionType,
    key: keyDown
  }
}

export function handleInputKeyUp(keyUp) {
  let actionType;
  if (MOVEMENT_KEYS.has(keyUp)) {
    actionType = MOVE_KEY_UP
  } else {
    actionType = UNHANDLED
  }
  return {
    type: actionType,
    key: keyUp
  }
}

export function loopTick() {
  return {
    type: TICK
  }
}


