import {Map, List} from 'immutable';

import doCirclesOverlap from '../api/doCirclesOverlap';

const LANDING_RANGE = 50;
const HAILING_RANGE = 100;

export function landOnPlanet(state, ship) {
  const planets = state.get('planets');
  const closestPlanet = findClosestPlanetInHailing(planets, ship);
  if (!closestPlanet) {
    return state;
  }
  const messageCount = state.getIn(['messages', 'count']);
  let updatedMsgCount = messageCount + 1;
  const hailResult = hailPlanet(closestPlanet, ship, updatedMsgCount);
  if (hailResult.get('status') === 'ok') {
    const existingMsgs = state.getIn(['messages', 'list']);
    if (withinRange(closestPlanet, ship, LANDING_RANGE)) {
      const landingResult = attemptLanding(closestPlanet, ship, messageCount);
      const newMsg = landingResult.get('message');
      const previousHailMessageIndex = existingMsgs.findIndex((message) => {
        return message.get('text') == 'cleared for landing';
      });
      const existingMsgsWithoutHail =
        previousHailMessageIndex != -1
          ? existingMsgs.delete(previousHailMessageIndex)
          : existingMsgs;
      const updatedMsgs = existingMsgsWithoutHail.concat([newMsg]);
      return state.setIn(['messages', 'list'], updatedMsgs);
    }
    const newMsg = hailResult.get('message');
    const updatedMsgs = existingMsgs.concat([newMsg]);
    return state.setIn(['messages', 'list'], updatedMsgs);
  }
  return state;
}

function attemptLanding(planet, ship, messageCount) {
  return new Map({
    status: 'ok',
    message: new Map({
      text: `Welcome to ${planet.get('name')}`,
      ticks: 80,
      id: messageCount + 1
    })
  })
}

function hailPlanet(planet, ship, messageCount) {
  if (allowedToLand(planet, ship)) {
    return new Map({
      status: 'ok',
      message: new Map({
        text: 'cleared for landing',
        ticks: 200,
        id: messageCount + 1
      })
    })
  }
  return new Map({
    status: 'not_allowed',
    message: new Map({
      text: 'not allowed to land',
      id: messageCount + 1
    })
  });
}

function allowedToLand(planet, ship) {
  return true;
}

function findClosestPlanetInHailing(planets, ship) {
  const planetsWithinRangeOfShip = planets.filter((planet) =>
    withinRange(planet, ship, HAILING_RANGE)
  );
  // TODO: when there's multiple planets in range, do something
  // smarter here and pick the closest one
  const closestPlanet =
    planetsWithinRangeOfShip.size == 1
      ? planetsWithinRangeOfShip.first()
      : planetsWithinRangeOfShip.reduce((closestPlanetSoFar, planet) => {
      return closestPlanetSoFar;
    }, planetsWithinRangeOfShip.get(0));
  return closestPlanet;
}

function withinRange(planet, ship, range) {
  // compare circles using the range plus the radii of the planet and ships
  const planetX = planet.get('positionX');
  const planetY = planet.get('positionY');
  const planetHeight = planet.get('height');
  const planetWidth = planet.get('width');
  const shipX = ship.get('positionX');
  const shipY = ship.get('positionY');
  const shipHeight = ship.get('height');
  const shipWidth = ship.get('width');

  const planetRadius = planetWidth >= planetHeight ? planetWidth / 2 : planetHeight / 2;
  const shipRadius = shipWidth >= shipHeight ? shipWidth / 2 : shipHeight / 2;
  const planetCircle = {
    center: {top: planetY + planetRadius, left: planetX + planetRadius},
    radius: planetRadius + range
  };
  const shipCircle = {
    center: {top: shipY + shipRadius, left: shipX + shipRadius},
    radius: shipRadius
  };
  return doCirclesOverlap(planetCircle, shipCircle);
}


