export function onTickMessages(state) {
  // decrement the ticks on each message
  const messages = state.getIn(['messages', 'list']);
  const updatedMessages = messages
    // filter out messages that will expire on this tick
    .filter((message) => {
      const ticks = message.get('ticks');
      return !(ticks - 1 < 0);
    })
    .map((message) => {
      const ticks = message.get('ticks');
      if (ticks) {
        return message.set('ticks', ticks - 1);
      }
      return message;
    });
  return state.setIn(['messages', 'list'], updatedMessages);
}


