import React, {PureComponent} from 'react';
import {connect} from 'react-redux';
import {addShip, handleInputKeyDown, handleInputKeyUp, loopTick} from './api/actions';

import Ship from './components/Ship';
import Bullet from './components/Bullet';
import Planet from './components/Planet';
import Message from './components/Message';

const LOOP_INTERVALS = 35;

class AppComponent extends PureComponent {

  constructor(props) {
    super(props);

    this._loopInterval = null;

    this.state = {
      loopRequest: 0
    };

    this.onButtonClick = this._onButtonClick.bind(this);
    this.onKeyDown = this._onKeyDown.bind(this);
    this.onKeyUp = this._onKeyUp.bind(this);
  }

  render() {
    return (
      <div className="App">
        <div className="interface">
          <div className="top-bar">
            <button className="menu-button" onClick={() =>
              this.onButtonClick()}>
              Add Ship
            </button>
          </div>
          <div className="message-bar">
            {this._renderMessages()}
          </div>
        </div>
        <Ship data={this.props.gameLoop.get('ship')}/>
        {this._renderShips()}
        {this._renderProjectiles()}
        {this._renderPlanets()}
      </div>
    );
  }

  _renderMessages() {
    return this.props.gameLoop
      .getIn(['messages', 'list'])
      .map((message, index) =>
        <Message key={message.get('id')} data={message}/>
      )
  }

  _renderPlanets() {
    return this.props.gameLoop.get('planets').map((planet) => {
      return <Planet key={planet.get('id')} data={planet}/>
    });

  }

  _renderShips() {
    return this.props.gameLoop.get('ships').map((ship, index) => {
      return <Ship key={index} data={ship}/>
    });

  }

  _renderProjectiles() {
    return this.props.gameLoop.get('projectiles').map((projectile, index) => {
      return <Bullet key={index} data={projectile}/>
    });
  }

  _onButtonClick() {
    this.props.onButtonClick()
  }

  _onKeyDown(event) {
    this.props.onKeyDown(event.key);
  }

  _onKeyUp(event) {
    this.props.onKeyUp(event.key);
  }

  _onLoopTick() {
    this.props.onLoopTick();
    // this.setState({
    //   loopRequest: requestAnimationFrame(this.props.onLoopTick)
    // });
  }

  componentDidMount() {
    window.addEventListener("keydown", this.onKeyDown);
    window.addEventListener("keyup", this.onKeyUp);
    // this.setState({
    //   loopRequest: requestAnimationFrame(this.props.onLoopTick)
    // });
    this._loopInterval = window.setInterval(this.props.onLoopTick, LOOP_INTERVALS);
  }

  componentWillUnmount() {
    window.removeEventListener("keydown", this.onKeyDown);
    window.removeEventListener("keyup", this.onKeyUp);
    window.clearInterval(this._loopInterval);
    this._loopInterval = null;
  }
}

AppComponent.propTypes = {
  count: React.PropTypes.number,
  gameLoop: React.PropTypes.object
};

const mapStateToProps = (state) => {
  return {
    count: state.base.count,
    gameLoop: state.gameLoop
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    onButtonClick: () => {
      dispatch(addShip())
    },
    onKeyDown: (key) => {
      dispatch(handleInputKeyDown(key));
    },
    onKeyUp: (key) => {
      dispatch(handleInputKeyUp(key));
    },
    onLoopTick: () => {
      dispatch(loopTick());
    }
  }
};

const App = connect(
  mapStateToProps,
  mapDispatchToProps
)(AppComponent);

export default App;


