import {
  MOVE_KEY_DOWN, MOVE_KEY_UP, KEY_DOWN, KEY_UP, TICK,
  ADD_SHIP, ATTEMPT_TO_LAND
} from '../api/actions';

import initialState from '../initialState';

import {onTickProjectiles} from './onTickProjectiles';
import {onTickShip} from './onTickShip';
import {onTickShips} from './onTickShips';
import {onTickMessages} from './onTickMessages';
import {fireWeapon} from './fireWeapon';
import {landOnPlanet} from './landOnPlanet';
import {collisionDetector} from './collisionDetector';
import {collisionResolver} from './collisionResolver';

function onTick(state) {
  const updatedStateWithPlayerShipMovement = onTickShip(state);
  const updatedStateWithShipsMovement = onTickShips(updatedStateWithPlayerShipMovement);
  const updatedStateWithProjectileMovement = onTickProjectiles(updatedStateWithShipsMovement);
  const updatedStateWithCollisions = collisionDetector(updatedStateWithProjectileMovement);
  const updatedStateWithCollisionsResolved = collisionResolver(updatedStateWithCollisions);
  const updatedStateWithCollisionsMessagesUpdated = onTickMessages(updatedStateWithCollisionsResolved);
  return updatedStateWithCollisionsMessagesUpdated;
}

export default function gameLoop(state = initialState, action) {
  switch (action.type) {
    case MOVE_KEY_DOWN:
      return state.setIn(['keysDown', action.key], true);
    case MOVE_KEY_UP:
      return state.setIn(['keysDown', action.key], false);
    case KEY_DOWN:
      if (action.key === ' ') return fireWeapon(state);
      if (action.key === 'l') return landOnPlanet(state, state.get('ship'));
      break;
    case ATTEMPT_TO_LAND:
      break;
    case ADD_SHIP:
      return state.set('ships', state.get('ships').push(action.ship));
    case TICK:
      return onTick(state);
  }
  return state;
}


