import React, {PureComponent} from 'react';

import Sprite from './sprite';

class Ship extends PureComponent {

  constructor(props) {
    super(props);
    const height = this.props.data.get('height');
    const width = this.props.data.get('width');
    this.state = {
      src: `ship_${height}x${width}.png`,
      height,
      width
    }
  }

  render() {
    const data = this.props.data;
    const rotation = data.get('rotation');
    const positionX = data.get('positionX');
    const positionY = data.get('positionY');
    // console.log(`ship: ${rotation} @ (${positionX}, ${positionY})`)
    return (
      <div className="outline"
           style={{
             transform: `rotate(${rotation}deg)`,
             height: this.state.height,
             width: this.state.width,
             top: positionY,
             left: positionX,
             position: 'fixed'
           }}>
        <Sprite
          src={this.state.src}
          height={this.state.height}
          width={this.state.width}
          top={positionY}
          left={positionX}
        />
      </div>
    );
  }
}

export default Ship;


