import {calculatePositionChange} from './movement';

function filterVisibleProjectiles(projectile, index, projectiles) {
  const positionX = projectile.get('positionX');
  const positionY = projectile.get('positionY');
  // console.log(`filtering: (${positionX}, ${positionY})`);
  if (positionX > 1000 || positionX < -50 || positionY > 1000 || positionY < 0) {
    return false;
  }
  return true;
}

export function onTickProjectiles(state) {
  const projectiles = state.get('projectiles');
  const projectilesAfterPruningOffscreen = projectiles.filter(filterVisibleProjectiles);
  const stateWithVisibleProjectilesOnly = state.set('projectiles', projectilesAfterPruningOffscreen);

  const stateWithProjectilePositionChanges = projectilesAfterPruningOffscreen
    .reduce((updatedState, projectile, index) => {
      const {positionX, positionY} = calculatePositionChange(
        projectile.get('speed'),
        projectile.get('rotation'),
        projectile.get('positionX'),
        projectile.get('positionY'));

      let updatedProjectile = projectile.set('positionX', positionX).set('positionY', positionY);
      return updatedState.setIn(['projectiles', index], updatedProjectile)
    }, stateWithVisibleProjectilesOnly);

  return stateWithProjectilePositionChanges;
}


