export default function doCirclesOverlap(circle1, circle2) {
  // find the distance between the centers and compare to the sum of the radii
  const dx = circle1.center.top - circle2.center.top;
  const dy = circle1.center.left - circle2.center.left;
  // taking square roots is more expensive than squaring the sum of the radii
  const dSqr = (dx * dx) + (dy * dy);
  const radiusSumSqr = (circle1.radius + circle2.radius) * (circle1.radius + circle2.radius);
  if (dSqr - radiusSumSqr < 500) {
    // console.log(`difference between ${circle1.center.top} and ${circle2.center.top}`);
    // console.log(`difference between ${circle1.center.left} and ${circle2.center.left}`);
    // console.log(`collision close: ${dSqr} vs ${radiusSumSqr}`)
  }
  return dSqr <= radiusSumSqr;
}


